.wrapper {
    padding-top: 50px;
    background-image: url(/Skater-Directory/static/media/423562.b768e234.png);
    background-size: cover;
    background-repeat: no-repeat;
    height: 100%;
    display: flex;
    flex-flow: row wrap;
    padding: 20px;
    justify-content: center;
    align-content: flex-start;
    overflow: auto;
   
  }
  h1 {
    padding-top: 20px;
    padding-bottom: 20px;
    justify-content: center;
    text-align: center;
    background-color: purple;
    color: white;
}

.searchbar {
    align-items: center;
}
img {
    height: 200px;
    width: 200px;
}
table {
    padding-top: 50px;
    /* padding-left: 100px; */
    color: white;
    text-shadow: -1px -1px 0 #000, 1px -1px 0 #000, -1px 1px 0 #000, 1px 1px 0 #000;
}

tr {
    padding: 50px;

    color: white;
    text-shadow: -1px -1px 0 #000, 1px -1px 0 #000, -1px 1px 0 #000, 1px 1px 0 #000;
}


td {
padding: 50px;
margin-right: 50px;
color: white;
text-shadow: -1px -1px 0 #000, 1px -1px 0 #000, -1px 1px 0 #000, 1px 1px 0 #000;
}

image {
    display: block;
    height: 100px;
    width: 100px;
}

